.user-info {
  display: flex;
  flex-direction: column;
  row-gap: 8px;

  &__item {
    position: relative;
    width: fit-content;
    padding-bottom: 4px;
    cursor: pointer;

    &::after {
      content: "";
      position: absolute;
      height: 3px;
      width: 100%;
      left: 0;
      bottom: 0;
      transition: background-color 0.3s;
    }

    &:hover::after {
      background-color: lightslategray;
    }
  }
}